import React from 'react'
import mapboxgl from 'mapbox-gl'
import {ScaleControl} from 'mapbox-gl';
import { getDataFromServer } from '../lib/connect-api';
import './Map.css';

mapboxgl.accessToken = 'pk.eyJ1IjoibWFwYm94IiwiYSI6ImNpejY4M29iazA2Z2gycXA4N2pmbDZmangifQ.-g_vE53SD2WrJ6tFX7QHmA';

class Map extends React.Component {
  /*
   * Аналогично Menu в state хранениться текущая
   * комманда к API от пользователя, для передачи
   * в обработчик http-запросов к серверу API 
   */ 
  constructor(props: Props) {
    super(props);
    this.state = {
      'latitude' : 55.7558,
      'longitude' : 37.6,
      'accuracy' : 0,
      zoom : 12
    };
  }
  /*
   * Обработка запроса, переданных через props.
   * В случае изинения props вызвается обработчик.
  */
  componentDidUpdate(prevProps) {
    if (this.props.latitude !== prevProps.latitude || this.props.longitude !== prevProps.longitude) {
      console.log('ggg');
      this.map.flyTo({
          center: [
            this.props.longitude,
            this.props.latitude],
          zoom: 18
      });
      this.map.addLayer({
        "id": "0",
        "type": "symbol",
        "source": {
            "type": "geojson",
            "data": {
                "type": "FeatureCollection",
                "features": [{
                    "type": "Feature",
                    "geometry": {
                        "type": "Point",
                        "coordinates": [this.props.longitude, this.props.latitude]
                    },
                    "properties": {
                        "title": "YOU!",
                        "icon": "monument"
                    }
                }]
            }
        },
        "layout": {
            "icon-image": "{icon}-15",
            "text-field": "{title}",
            "text-font": ["Open Sans Semibold", "Arial Unicode MS Bold"],
            "text-offset": [0, 0.6],
            "text-anchor": "top"
        }
    });
    }
  }


  componentDidMount() {
    let {latitude, longitude, accuracy, zoom} = this.state;
    this.map = new mapboxgl.Map({
      container: this.mapContainer,
      style: 'mapbox://styles/mapbox/streets-v9',
      center: [longitude, latitude],
      zoom: zoom
    });

   this.map.on('load', () => {
        this.map.addControl(new mapboxgl.NavigationControl());
        console.log('HERE');  
    });
  }

  render() {
    return (
      <div ref={el => this.mapContainer = el} className='Map' />
    );
  }
}

export default Map