import React from 'react'
import { Button, InputGroup, InputGroupAddon, InputGroupText, Input, FormGroup,  Label, Form, CustomInput, Container, Col, Row} from 'reactstrap';
import { getUserGeoData } from '../lib/get-userGeoData';
import Map from './Map'
import './Menu.css'

const options = {
  enableHighAccuracy: true,
  timeout: 5000,
  maximumAge: 0
};

class Menu extends React.Component {
  /* 
   * В state храним геоданные для передачи в качестве одного из аргументов 
   * в запросе к API. Через props передаем геоданные в Map, где происходит
   * дальнейшая генерация http-запроса к API и обработка полученного ответа от сервера для рендеринга на карте.
   */
  constructor(props: Props) {
    super(props);
    this.state = {
      searchField : '',
      isToggleOn: false,
      isFetched: false,
      inputLongitude: 0,
      inputLatitude: 0,
      geodata: {}
    };
    this.handleClick = this.handleClick.bind(this);
  };

  handleClick() {
    this.setState(state => ({
      isToggleOn: !state.isToggleOn
    }));
    console.log(this.state.isToggleOn)
  }

  handleChangeSearch (event) {
    const changedValue = event.target.value;
    console.log(changedValue);
    this.setState({searchField : changedValue});
    console.log(this.state);
  }

  handleChangeLatitude(event) {
    const changedValue = event.target.value;
    console.log(changedValue);
    this.setState({inputLatitude: changedValue});
    console.log(this.state);
  }

  handleChangeLongitude(event) {
    const changedValue = event.target.value;
    console.log(changedValue);
    this.setState({inputLongitude: changedValue});
    console.log(this.state);
  }

  goSearch = () => {
    console.log('GO!');

    getUserGeoData(options)
      .then(res => this.setState({geodata : res, isFetched : true}))
      .catch(err =>  console.warn('ERROR(${err.code}): ${err.message}'));
    console.log(this.state['geodata']);
  }

  render() {
    const isToggleOn = this.state.isToggleOn;
    const isFetched = this.state.isFetched;
    const latitude = this.state.geodata['latitude'];
    const longitude = this.state.geodata['longitude'];
    return (
    	<div>
    	  <div className='menu'>
        {/* Шапка Карточки 
          <h1>
            Геокодер
            <span role="img" aria-label="sheep">🐑</span>  
          </h1>
        */}
        {/* Подложка */}
        <div className="outer-div">
          <div className="inner-div">

          {/* Элемент поиска */}
          <FormGroup>
            <Label for="addressValue" hidden>Адрес</Label>
            <Input 
              type="text"
              name="addressValue"
              id="addressValue"
              placeholder="Адрес"
              onChange={event => this.handleChangeSearch(event)}
            />
          </FormGroup>

          {/* Элемент ввода геоданных */}
          {isToggleOn ?
            (<FormGroup>
              <Label for="latitudeValue" hidden>Широта</Label>
              <Input
                type="number"
                name="latitudeValue"
                id="latitudeValue"
                placeholder="Широта"
                onChange={event => this.handleChangeLatitude(event)}
              />
              <Label for="longitudeValue" hidden>Долгота</Label>
              <Input
                type="number"
                name="password"
                id="longitudeValue"
                placeholder="Долгота"
                onChange={event => this.handleChangeLongitude(event)}
              />
            </FormGroup>) 
            : ''
          }

          {/* Контейнер элементов контроля формы */}
          <Container>
            <Row>
              <Col xs="3">
                <Button onClick={this.goSearch}>Поиск</Button>
              </Col>
              <Col xs="3"></Col>
              <Col xs="3">
                <CustomInput type="checkbox" id="exampleCustomCheckbox" onClick={this.handleClick} label="геоточка"/>
              </Col>
            </Row>
          </Container>


          {/* Элемент вывода ответа от геокодера и заданных координат */}
          {isFetched ?
            (<div className="output-div">
              <p>{latitude.toFixed(4)}   {longitude.toFixed(4)}</p>
            </div>) 
            : ''
          }
          </div>
        </div>
    	  </div>
        {/* Компонент Карта */}
        <Map latitude={this.state.geodata['latitude']} longitude={this.state.geodata['longitude']} accuracy={this.state.geodata['accuracy']} />
    	</div>
    );
  }
}
// <Map latitude={this.state.geodata['latitude']} longitude={this.state.geodata['longitude']} accuracy={this.state.geodata['accuracy']} />
// <Map latitude={this.state.latitude} longitude={this.state.longitude} accuracy={this.state.accuracy} />
export default Menu


